import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"
import { rhythm } from "../utils/typography"

const Header = styled.div`
  background: #ffcdc4;
  .top {
    max-width: 400px;
    padding: 0 20px;
    margin: 0 auto;
    img {
      width: 100%;
      margin: 20px auto 0 auto;
    }
  }
`

const Links = styled.div`
  display: flex;
  justify-content: center;
  a {
    padding: ${rhythm(1)};
  }
`
const BottomLink = styled.div`
  width: 100%;
  align-items: bottom;
  display: flex;
  a {
    padding: ${rhythm(1)};
  }
  p {
    @media (max-width: 700px) {
      padding: 0 10px;
    }
  }
`

const Main = styled.div`
  max-width: 800px;
  margin: 0 auto;
  @media (max-width: 700px) {
    padding: 0 10px;
  }
`

class Layout extends React.Component {
  render() {
    const { location, title, subtitle, children } = this.props
    const rootPath = `${__PATH_PREFIX__}/`
    const url = "https://divergeweekly"
    const isPost =
      location.pathname && String(location.pathname).split("/").length <= 3
    const isMail =
      location.pathname && String(location.pathname).includes("/mail")

    return (
      <>
        <Header>
          <div className="top">
            <Link to={`${url}/`}>
              <img src={`${url}/logo.png`} />
            </Link>
          </div>
          {!isMail && (
            <Links>
              <Link to={`${url}/`}>Issues</Link>
              <Link to={`${url}/sponsor`}>Sponsor</Link>
              <Link to={`${url}/about`}>About</Link>
            </Links>
          )}
        </Header>
        <Main>{children}</Main>
      </>
    )
  }
}

export default Layout
